import {mapState} from "vuex"
import {deleteArticle} from "@/api/article";
export default {
  name: "article-meta",
  props: {
    article: {
      type: Object,
      required: true,
    }
  },
  computed: {
    ...mapState(['user']),
  },
  methods: {
    async articleDelete() {
      await deleteArticle(this.article.slug)
      await this.$router.back()
    }
  }
}
