import {getComments} from "@/api/article"
import {mapState} from "vuex"

export default {
  name: "article-comments",
  props: {
    slug: {
      type: String,
      required: true,
    }
  },
  data() {
    return {
      comments: []
    }
  },
  computed: {
    ...mapState(['user']),
  },
  async mounted() {
    const {data} = await getComments(this.slug)
    this.comments = data.comments
  }
}
